div.tfw-view-InputColor {
    display: inline-block;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    margin: .5rem;
}

div.tfw-view-InputColor.wide {
    display: block;
}

div.tfw-view-InputColor>div.summary>div.tfw-view-Slider {
    flex: 1 1 auto;
    min-width: 110px;
}

div.tfw-view-InputColor>div.summary>div.tfw-view-Touchable {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: stretch;
}

div.tfw-view-InputColor>div.summary>div.tfw-view-Touchable>div {
    max-height: 2rem;
    min-height: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 0 0 auto;
    line-height: 2rem;
    font-family: monospace;
    font-weight: bolder;
    min-width: 8rem;
    max-width: 8rem;
}

div.tfw-view-InputColor>div.summary>div.tfw-view-Touchable>div>div {
    margin: 0 .5rem;
}

div.tfw-view-InputColor>div.summary>div.tfw-view-Touchable>input.invalid {
    font-weight: normal;
    text-decoration: line-through;
}
