div.tfw-view-Progress {
    display: inline-block;
    padding: 0;
    min-width: 1rem;
}

div.tfw-view-Progress.wide {
    display: block;
    width: 100%;
}

div.tfw-view-Progress > div.bar {
    position: relative;
    border-radius: 4px;
    border: 1px solid var(--thm-black-3);
    overflow: hidden;
}

div.tfw-view-Progress > div.bar > div {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background-image: linear-gradient(to bottom,var(--thm-bgSD),var(--thm-bgS) 80%,var(--thm-bgSL));
    font-size: 0;
    transition: transform .5s;
}

div.tfw-view-Progress > div.bar > div::after {
    transition: transform .3s linear;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    height: 50%;
    content: ".";
    font-size: 0;
    background-color: var(--thm-white-7);
}
