.tfw-view-OptionalInput {
    display: inline-flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.tfw-view-OptionalInput.wide {
    display: flex;
    width: 100%;
}

.tfw-view-OptionalInput > *:first-child {
    flex: 1 1 auto;
    order: 0;
}

.tfw-view-OptionalInput > *:last-child {
    flex: 0 0 auto;
    order: 1;
}

.tfw-view-OptionalInput.reverse > *:last-child {
    order: -1;
}
